<template>
  <ec-container title="进口烟计划衔接量推送" icon="onetomany" color-code="001">
    <ec-container-body>
      <ec-panel>
        <ec-panel-toolbar>
          <ec-panel-item>
            <ec-panel-item-right style="font-size: 16px"
              >业务年度: {{ busiYear }}</ec-panel-item-right
            >
          </ec-panel-item>
          <ec-panel-item>
            <ec-query-panel>
              <ec-query-item label="国别">
                <ec-select
                  showSearch
                  allowClear
                  placeholder="请选择"
                  v-model="listQuery.countryCode"
                >
                  <a-select-option v-for="item in countryCodeList" :key="item.enumKey">
                    {{ item.name }}
                  </a-select-option>
                </ec-select>
              </ec-query-item>
              <ec-query-item label="发送状态">
                <ec-select showSearch allowClear placeholder="请选择" v-model="listQuery.sendState">
                  <!-- <a-select-option value="1" title="启用"> 启用 </a-select-option>
                  <a-select-option value="0" title="停用"> 停用 </a-select-option> -->
                  <a-select-option v-for="item in sendStateList" :key="item.enumKey">
                    {{ item.name }}
                  </a-select-option>
                </ec-select>
              </ec-query-item>
              <ec-query-item label="计量单位">
                <ec-select
                  showSearch
                  allowClear
                  placeholder="请选择"
                  v-model="listQuery.weightUnit"
                >
                  <!-- <a-select-option value="1" title="启用"> 启用 </a-select-option>
                  <a-select-option value="0" title="停用"> 停用 </a-select-option> -->
                  <a-select-option v-for="item in weightUnitList" :key="item.enumKey">
                    {{ item.name }}
                  </a-select-option>
                </ec-select>
              </ec-query-item>
              <!-- <template #inner>
                <ec-query-item label="办理依据">
                  <a-input v-model="listQuery.remark" placeholder="请输入" />
                </ec-query-item>
              </template> -->
              <template #action>
                <a-button type="primary" @click="handleQuery">查询</a-button>
                <a-button @click="handleSeniorQuery">高级</a-button>
              </template>
            </ec-query-panel>
          </ec-panel-item>
          <ec-divider />
        </ec-panel-toolbar>

        <ec-panel-body>
          <ec-v-layout>
            <ec-v-item>
              <ec-panel-toolbar>
                <a-button type="primary" @click="push">推送</a-button>
              </ec-panel-toolbar>
            </ec-v-item>
            <ec-v-item>
              <ec-table
                ref="table"
                :data="tableData"
                :loading="loading"
                border
                @page-action="handlePagination"
                :pagination="pagination"
                :footer-method="footerMethod"
                show-footer
                @checkbox-all="selectAllEvent"
                @checkbox-change="selectChangeEvent"
              >
                <ec-table-col width="60" type="checkbox" align="center"></ec-table-col>
                <ec-table-col title="序号" width="80" type="seq" align="center"></ec-table-col>
                <ec-table-col title="需方单位" field="demandOrgName" align="center"></ec-table-col>
                <ec-table-col title="供方单位" field="supplyOrgName" align="center"></ec-table-col>
                <ec-table-col title="国别" field="countryCode" align="center" #default="{ row }">
                  {{ onGetleafCountryName(row.countryCode, countryCodeList) }}</ec-table-col
                >
                <ec-table-col
                  title="烟叶类型"
                  field="leafTypeCode"
                  align="center"
                  #default="{ row }"
                  >{{ onGetleafTypeName(row.leafTypeCode, leafTypeCodeList) }}</ec-table-col
                >
                <ec-table-col
                  title="烟叶形态"
                  field="leafFormCode"
                  align="center"
                  #default="{ row }"
                  >{{ onGetleafFormName(row.leafFormCode, leafFormCodeList) }}</ec-table-col
                >
                <ec-table-col title="计划衔接量" field="apprWeight" align="center"></ec-table-col>
                <ec-table-col title="办理依据" field="remark" align="center"></ec-table-col>
                <ec-table-col title="推送状态" field="transStates" align="center">
                  <template #default="{ row }">
                    <div v-if="row.transStates === '0'">
                      <span>未推送</span>
                    </div>
                    <div v-if="row.transStates === '1'">
                      <span>推送成功</span>
                    </div>
                    <div v-else>
                      <span>推送失败</span>
                    </div>
                  </template>
                </ec-table-col>
                <ec-table-col title="操作" #default="{ row }" align="center">
                  <a-button type="link" @click="onDetails(row)">详细</a-button>
                </ec-table-col>
              </ec-table>
            </ec-v-item>
          </ec-v-layout>
        </ec-panel-body>
      </ec-panel>
      <add-modal
        ref="addModal"
        :operateTypeList="operateTypeList"
        :weightUnitList="weightUnitList"
        :countryCodeList="countryCodeList"
        :leafTypeCodeList="leafTypeCodeList"
        :leafFormCodeList="leafFormCodeList"
        :transStatesList="transStatesList"
      ></add-modal>
      <small-modal
        ref="smallRef"
        :operateTypeList="operateTypeList"
        :countryCodeList="countryCodeList"
        :leafTypeCodeList="leafTypeCodeList"
        :leafFormCodeList="leafFormCodeList"
        :transStatesList="transStatesList"
        :weightUnitList="weightUnitList"
        @queryPage="queryPage"
      ></small-modal>
    </ec-container-body>
  </ec-container>
</template>

<script>
import addModal from './AddModel.vue';
// import { OrgPicker } from '@euler-material/ecm-ui';
import smallModal from './SmallModel.vue';

export default {
  name: 'dailyImport',
  components: {
    addModal,
    // OrgPicker,
    smallModal,
  },
  data() {
    return {
      listQuery: {},
      tableData: [],
      importPlanIds: [],
      countryCodeList: [],
      sendStateList: [],
      operateTypeList: [],
      weightUnitList: [],
      leafTypeCodeList: [],
      leafFormCodeList: [],
      transStatesList: [],
      busiYear: new Date().getFullYear(),
      loading: false,
      pagination: {
        pageNumber: 1,
        pageSize: 10,
        total: 0,
      },
    };
  },
  computed: {},
  methods: {
    // 勾选
    selectAllEvent({ checked, records }) {
      // console.log(checked ? '所有勾选事件' : '所有取消事件', records);
      if (checked) {
        console.log(records);
        records.forEach((item) => {
          this.importPlanIds.push(item.importPlanId);
        });
      } else {
        this.importPlanIds = [];
      }
    },
    selectChangeEvent({ checked, records }) {
      if (checked) {
        console.log(records[0].supplyOrgCode);
        this.importPlanIds.push(records[0].importPlanId);
      } else {
        this.importPlanIds.splice(this.importPlanIds.indexOf(records[0].importPlanId));
      }
      // console.log(this.importPlanIds);
    },
    // 推送
    push() {
      console.log('ts');
      this.$apis.apm.dailyImport
        .updatePushPcTAsImportAppr({ importPlanIds: this.importPlanIds })
        .then((res) => {
          console.log(res);
        })
        .catch((err) => {
          console.log(err);
        });
    },
    // 详细查询
    onDetails(row) {
      this.$refs.addModal.show(row);
    },
    handleReset() {},
    // 高级查询请求分页
    queryPage(newForm) {
      // console.log(newForm);
      this.$apis.apm.dailyImport
        .queryPagePcTAsImportAppr({
          ...newForm,
          demandOrgCode: newForm.demandOrgCode[0],
          pageNo: this.pagination.pageNumber,
          pageSize: this.pagination.pageSize,
          busiYear: this.busiYear,
        })
        .then((res) => {
          console.log(res);
          this.tableData = res.rows;
        });
    },
    // 请求分页查询
    async getTableData() {
      try {
        const res = await this.$apis.apm.dailyImport.queryPagePcTAsImportAppr({
          ...this.listQuery,
          pageNo: this.pagination.pageNumber,
          pageSize: this.pagination.pageSize,
          busiYear: this.busiYear,
        });
        console.log(res);
        this.tableData = res.rows;
        this.pagination.total = res.totalRows;
      } catch (err) {
        console.log('err', err.message);
      }
    },
    handlePagination(pageNumber, pageSize) {
      this.pagination.pageNumber = pageNumber;
      this.pagination.pageSize = pageSize;
    },
    // 查询
    handleQuery() {
      this.getTableData();
    },
    // 高级查询
    handleSeniorQuery() {
      this.$refs.smallRef.show();
    },
    // 枚举数据获取
    init() {
      this.getEnumType();
      this.querySpinnerLeafType();
      this.querySpinnerLeafForm();
      this.listQueryCmTLeafCountry(); // 国别
    },
    getEnumType() {
      this.$apis.enum.index
        .getEnumType({
          enumTypeCodes: [
            'OPERATE_TYPE',
            'WEIGHT_UNIT',
            // 'COUNTRY_CD',
            'SEND_STATE',
            // 'LEAF_TYPE_CD',
            // 'LEAF_FORM_CD',
            'INFORM_PUSH_TYPE_EK',
          ],
        })
        .then((res) => {
          if (res) {
            console.log(res.enumMap);
            this.operateTypeList = res.enumMap.OPERATE_TYPE;
            this.weightUnitList = res.enumMap.WEIGHT_UNIT;
            // this.countryCodeList = res.enumMap.COUNTRY_CD;
            this.sendStateList = res.enumMap.SEND_STATE;
            // this.leafTypeCodeList = res.enumMap.LEAF_TYPE_CD;
            // this.leafFormCodeList = res.enumMap.LEAF_FORM_CD;
            this.transStatesList = res.enumMap.INFORM_PUSH_TYPE_EK;
          }
        })
        .catch((err) => {
          console.log(err);
        });
    },
    querySpinnerLeafType() {
      this.$apis.apm.dailyImport
        .querySpinnerLeafType({})
        .then((res) => {
          console.log(res);
          this.leafTypeCodeList = res.rows;
        })
        .catch((err) => {
          console.log(err);
        });
    },
    querySpinnerLeafForm() {
      this.$apis.apm.dailyImport
        .querySpinnerLeafForm({})
        .then((res) => {
          console.log(res);
          this.leafFormCodeList = res.rows;
        })
        .catch((err) => {
          console.log(err);
        });
    },
    listQueryCmTLeafCountry() {
      this.$apis.apm.dailyImport
        .listQueryCmTLeafCountry({})
        .then((res) => {
          console.log(res);
          this.countryCodeList = res.rows;
        })
        .catch((err) => {
          console.log(err);
        });
    },
    // 计算合计的数据
    sumNum(list, field) {
      let count = 0;
      list.forEach((item) => {
        count += Number(item[field]);
      });
      return count.toFixed(2);
    },
    // 底部的合计
    footerMethod({ columns, data }) {
      // console.log(columns, data);
      return [
        columns.map((column, columnIndex) => {
          if (columnIndex === 0) {
            return '合计';
          }
          if (['apprWeight'].includes(column.property)) {
            return this.sumNum(data, column.property);
          }
          return null;
        }),
      ];
    },
    // 匹配
    onGetleafCountryName(key, data) {
      return data?.find((item) => item.leafCountryCode === key)?.leafCountryName;
    },
    onGetleafTypeName(key, data) {
      return data?.find((item) => item.leafTypeCode === key)?.leafTypeName;
    },
    onGetleafFormName(key, data) {
      return data?.find((item) => item.leafFormCode === key)?.leafFormName;
    },
  },
  created() {
    this.init();
    this.handleQuery();
  },
};
</script>

<style></style>
