<template>
  <ec-drawer
    title="详细"
    :visible="visible"
    size="large"
    @close="cancel"
    class="ec-draw-form"
    :is-confirm="true"
  >
    <ec-container>
      <ec-container-body>
        <ec-panel title="第一步: 选取供需方单位、操作类型" color-code="003">
          <ec-panel-item>
            <ec-form ref="modelAddForm" :model="ruleForm" :rules="rules">
              <a-row type="flex" :gutter="12">
                <!-- <a-col :span="8">
                  <a-form-model-item label="供方单位" prop="supplyOrgCode">
                    <OrgPicker v-model.trim="ruleForm.supplyOrgCode"></OrgPicker>
                  </a-form-model-item>
                </a-col> -->
                <a-col :span="8">
                  <a-form-model-item label="需方单位" prop="demandOrgCode">
                    <!-- <ec-org-picker allow-clear v-model="ruleForm.demandOrgCode" /> -->
                    <OrgPicker v-model.trim="ruleForm.demandOrgCode"></OrgPicker>
                  </a-form-model-item>
                </a-col>
                <a-col :span="8">
                  <a-form-model-item label="操作类型" prop="operateType">
                    <a-select
                      allowClear
                      v-model="ruleForm.operateType"
                      placeholder="请选择操作类型"
                    >
                      <a-select-option
                        v-for="item in operateTypeList"
                        :key="item.enumKey"
                        :value="item.name"
                        >{{ item.name }}</a-select-option
                      >
                    </a-select>
                  </a-form-model-item>
                </a-col>
                <a-col :span="8">
                  <a-form-model-item label="国别" prop="countryCode">
                    <a-select allowClear v-model="ruleForm.countryCode" placeholder="请选择国别">
                      <a-select-option
                        v-for="item in countryCodeList"
                        :key="item.enumKey"
                        :value="item.name"
                        >{{ item.name }}</a-select-option
                      >
                    </a-select>
                  </a-form-model-item>
                </a-col>
                <a-col :span="8">
                  <a-form-model-item label="烤烟类型" prop="leafTypeCode">
                    <a-select
                      allowClear
                      v-model="ruleForm.leafTypeCode"
                      placeholder="请选择烤烟类型"
                    >
                      <a-select-option
                        v-for="item in leafTypeCodeList"
                        :key="item.enumKey"
                        :value="item.name"
                        >{{ item.name }}</a-select-option
                      >
                    </a-select>
                  </a-form-model-item>
                </a-col>
                <a-col :span="8">
                  <a-form-model-item label="烟叶形态" prop="leafFormCode">
                    <a-select
                      allowClear
                      v-model="ruleForm.leafFormCode"
                      placeholder="请选择烟叶形态"
                    >
                      <a-select-option
                        v-for="item in leafFormCodeList"
                        :key="item.enumKey"
                        :value="item.name"
                        >{{ item.name }}</a-select-option
                      >
                    </a-select>
                  </a-form-model-item>
                </a-col>
              </a-row>
              <a-row type="flex" :gutter="12">
                <a-col :span="24">
                  <a-form-model-item label="办理依据" prop="remark">
                    <a-input
                      v-model="ruleForm.remark"
                      placeholder="请输入办理依据"
                      autocomplete="off"
                    />
                  </a-form-model-item>
                </a-col>
              </a-row>
            </ec-form>
          </ec-panel-item>
        </ec-panel>
        <ec-panel title="步骤二: 录入/编辑数量" color-code="003">
          <ec-panel-item>
            <ec-table
              ref="xTable"
              border
              auto
              height=""
              :edit-config="{ trigger: 'click', mode: 'row', showStatus: true }"
              :data="tableData"
            >
              <ec-table-col title="调整前量" field="materialWeight" align="center">
                <template #edit="{ row }">
                  <a-input v-model="row.materialWeight"></a-input> </template
              ></ec-table-col>
              <ec-table-col title="本次调整量" field="materialProdWeight" align="center">
                <template #edit="{ row }">
                  <a-input v-model="row.materialProdWeight"></a-input>
                </template>
              </ec-table-col>
              <ec-table-col title="调整后量" field="materialBuyWeight" align="center">
                <template #edit="{ row }">
                  <a-input v-model="row.materialBuyWeight"></a-input> </template
              ></ec-table-col>
            </ec-table>
          </ec-panel-item>
        </ec-panel>
      </ec-container-body>
      <ec-container-footer fixed>
        <a-button @click="cancel">关闭</a-button>
      </ec-container-footer>
    </ec-container>
  </ec-drawer>
</template>
<script>
import { OrgPicker } from '@euler-material/ecm-ui';

export default {
  name: 'AdminOrgManageEdit',
  components: {
    OrgPicker,
  },
  props: {
    operateTypeList: {
      type: Array,
    },
    countryCodeList: {
      type: Array,
    },
    leafTypeCodeList: {
      type: Array,
    },
    leafFormCodeList: {
      type: Array,
    },
  },
  data() {
    return {
      fileList: [],
      rowData: '',
      visible: false,
      checked: false,
      ruleForm: {},
      rules: {
        // memberOrgCode: { required: true, message: '请输入会员单位' },
        // operateType: { required: true, message: '请输入操作类型' },
        // planMakeOrg: { required: true, message: '请输入计量单位' },
        // remark: { required: true, message: '请输入办理依据' },
      },
      planMakeOrgList: [],
      tableData: [],
    };
  },
  computed: {},
  methods: {
    // 显示推窗
    show(row) {
      // console.log('row', row);
      this.visible = true;
      if (row) {
        // 请求详细信息
        this.$apis.apm.dailyImport
          .queryDetailPcTAsImportAppr({ importPlanId: row.importPlanId })
          .then((res) => {
            console.log(res);
            this.ruleForm = res;
            this.tableData = [
              {
                baApprWeight: res.baApprWeight,
                apprWeight: res.apprWeight,
                aaApprWeight: res.aaApprWeight,
              },
            ];
          })
          .catch((err) => {
            console.log(err);
          });
      }
    },
    // 取消
    cancel() {
      this.visible = false;
      // 清空form
      this.$refs.modelAddForm.resetFields();
    },
  },
  created() {},
};
</script>

<style scoped>
.tips {
  color: rgb(183, 177, 177);
  text-indent: 2em;
}
</style>
