<template>
  <ec-modal v-model="visibile" title="高级" size="small" destroyOnClose :is-confirm="true">
    <ec-form :model="form" :rules="rules" ref="modelAddForm">
      <a-row type="flex" :gutter="12">
        <a-col :span="12">
          <a-form-model-item ref="demandOrgCode" prop="demandOrgCode" label="需方单位">
            <!-- <a-input v-model="form.demandOrgCode" placeholder="请输入供方单位" /> -->
            <OrgPicker v-model.trim="form.demandOrgCode"></OrgPicker>
          </a-form-model-item>
        </a-col>
        <a-col :span="12">
          <a-form-item prop="operateType" label="操作类型">
            <a-select allowClear v-model="form.operateType" placeholder="请选择操作类型">
              <a-select-option
                v-for="item in operateTypeList"
                :key="item.enumKey"
                :value="item.name"
                >{{ item.name }}</a-select-option
              >
            </a-select>
          </a-form-item>
        </a-col>
        <a-col :span="12">
          <a-form-item prop="countryCode" label="国别">
            <a-select allowClear v-model="form.countryCode" placeholder="请选择国别">
              <a-select-option
                v-for="item in countryCodeList"
                :key="item.enumKey"
                :value="item.name"
                >{{ item.name }}</a-select-option
              >
            </a-select>
          </a-form-item>
        </a-col>
        <a-col :span="12">
          <a-form-item prop="leafTypeCode" label="烟叶类型">
            <a-select allowClear v-model="form.leafTypeCode" placeholder="请选择烟叶类型">
              <a-select-option
                v-for="item in leafTypeCodeList"
                :key="item.leafTypeCode"
                :value="item.leafTypeName"
                >{{ item.leafTypeName }}</a-select-option
              >
            </a-select>
          </a-form-item>
        </a-col>
        <a-col :span="12">
          <a-form-item prop="leafFormCode" label="烟叶形态">
            <a-select allowClear v-model="form.leafFormCode" placeholder="请选择烟叶形态">
              <a-select-option
                v-for="item in leafFormCodeList"
                :key="item.leafFormCode"
                :value="item.leafFormName"
                >{{ item.leafFormName }}</a-select-option
              >
            </a-select>
          </a-form-item>
        </a-col>
        <a-col :span="12">
          <a-form-item prop="transStates" label="推送状态">
            <a-select allowClear v-model="form.transStates" placeholder="请选择推送状态">
              <a-select-option
                v-for="item in transStatesList"
                :key="item.enumKey"
                :value="item.name"
                >{{ item.name }}</a-select-option
              >
            </a-select>
          </a-form-item>
        </a-col>
        <a-col :span="12">
          <a-form-item prop="weightUnit" label="计量单位">
            <a-select allowClear v-model="form.weightUnit" placeholder="请选择计量单位">
              <a-select-option
                v-for="item in weightUnitList"
                :key="item.enumKey"
                :value="item.name"
                >{{ item.name }}</a-select-option
              >
            </a-select>
          </a-form-item>
        </a-col>
        <a-col :span="12">
          <a-form-model-item prop="remark" label="办理依据">
            <a-input v-model="form.remark" placeholder="请输入办理依据" />
          </a-form-model-item>
        </a-col>
      </a-row>
    </ec-form>
    <template v-slot:footer>
      <a-button type="primary" @click="formSubmit">查找</a-button>
      <a-button @click="formReset">重置</a-button>
      <a-button @click="formCancel">关闭</a-button>
    </template>
  </ec-modal>
</template>

<script>
import { OrgPicker } from '@euler-material/ecm-ui';

export default {
  name: 'popUpWindowsAdd',
  components: {
    OrgPicker,
  },
  props: {
    operateTypeList: {
      type: Array,
    },
    weightUnitList: {
      type: Array,
    },
    countryCodeList: {
      type: Array,
    },
    leafTypeCodeList: {
      type: Array,
    },
    leafFormCodeList: {
      type: Array,
    },
    transStatesList: {
      type: Array,
    },
  },
  data() {
    return {
      visibile: false,
      form: {
        // memberOrgCode: '',
        // operateType: [],
        // weightUnit: [],
        // remark: '',
      },
      rules: {
        // memberOrgCode: {
        //   required: true,
        //   message: '品牌名称 必须填写',
        //   trigger: 'blur',
        // },
        // operateType: {
        //   required: true,
        //   message: '卷烟编码 必须填写',
        //   trigger: 'blur',
        // },
      },
    };
  },
  computed: {
    // 根据是否有id, 判断新增/编辑
    // title() {
    //   return this.form?.id ? '编辑' : '新增';
    // },
  },
  methods: {
    show() {
      this.visibile = true;
    },
    formReset() {
      this.$refs.modelAddForm.resetFields();
    },
    formCancel() {
      this.visibile = false;
      this.$refs.modelAddForm.resetFields();
    },
    formSubmit() {
      this.$refs.modelAddForm.validate((valid) => {
        if (valid) {
          // 1. 保存数据逻辑
          // this.$http.post('/pages', this.form).then(res => {
          //   this.$message.success('操作成功！')
          //   this.formCancel()
          // })
          console.log('验证通过');
          // this.formCancel();
        } else {
          console.log('验证未通过');
        }
      });
      const newForm = JSON.parse(JSON.stringify(this.form));
      console.log(newForm);
      this.$emit('queryPage', newForm);
      this.formCancel();
      // this.$apis.apm.dailyImport.queryPagePcTAsSliceApprTpl({
      //     ...this.form,
      //   });
    },
  },
};
</script>
